.TodoCounter {
  
  font-size: 24px;
  text-align: center;
  margin: 0;
  padding: 0px 0px 40px 0px;
  font-weight: normal;
  color: #ffffff;
}
   
.TodoCounter span {
  font-weight: bold;
}

@media (min-width: 770px) {
  .TodoCounter {
    
    font-size: 24px;
    text-align: center;
    margin: 0;
    padding: 48px;
    font-weight: normal;
    color: #ffffff;
  }
}