.TodoSearch {
    background: #F9FBFC;
    border-radius: 5px;
    border: 2px solid #ffffff;
    margin: 30px 24px 0px 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .TodoSearch::placeholder {
    color: #d8d8d8;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch:focus {
    outline: none; /* Eliminar el borde de enfoque predeterminado */
    border: 2px solid #008ca5; /* Establecer un borde más grueso y un color diferente */
    background-color: #f0f0f0; /* Cambiar el fondo cuando está enfocado */
    box-shadow: 0 0 5px rgba(0, 140, 165, 0.7); /* Agregar una sombra alrededor del elemento */
    transition: border-color 0.3s, background-color 0.3s, box-shadow 0.3s; /* Agregar transiciones suaves */
  }