.HideForm {
  display: none;
}
.app-container {
  margin: 20px;
  padding: 20px;
}
.logo-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px 50px 50px 50px;
}

img {
  width: 250px;
}

@media (min-width: 770px) {
  .HideForm {
    display: block;
  }
  .app-container {
    display: flex;
  }
  
  .left-column {
    width: 50%; /* Ancho de la columna izquierda */
    padding: 40px;
    /* Otros estilos de la columna izquierda */
  }
  
  .right-column {
    width: 50%; /* Ancho de la columna derecha */
    padding: 40px;
    /* Otros estilos de la columna derecha */
  }  
}