/* Estilo del botón en la versión móvil (hasta 770px) */
.CreateTodoButton {
    background-color: rgb(42, 38, 46);
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #fafafa;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    z-index: 1;
    transform: rotate(0);
    transition: .3s ease;
}

.CreateTodoButton:hover {
    transform: rotate(224deg);
}

.TareaText {
    display: none;
}

/* Estilo del botón en la versión de escritorio (mayor que 770px) */
@media (min-width: 771px) {
    .plus {
        display: none;
    }
    .TareaText {
        letter-spacing: 0.9px;
        font-weight: 600;
        display: block;
        margin-left: 0px;
        
    }
    .CreateTodoButton {
       display: none;
    }

    .CreateTodoButton:hover {
        background-color: rgb(0, 0, 0);
        transform: rotate(0);
    }
}