form {
  margin: 0;
  padding: 0px 0px 0px 0px;
}

textarea {
  background-color: #F9FBFC;
  border: 2px solid #202329;
  border-radius: 5px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #1E1E1F;
  font-size: 20px;
  text-align: left;
  padding: 12px;
  width: 100%; /* Ancho al 100% de la primera columna */
  max-width: 100%; /* Ancho máximo al 100% */
  height: 40px;
  resize: vertical; /* Permitir el redimensionamiento vertical del textarea */
}

textarea::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

textarea:focus {
  outline-color: #a003a5;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  border: 4px solid #ffffff;
  display: inline-block;
  font-size: 20px;
  color: #ffffff;
  font-weight: 400;
  width: 120px;
  height: 48px;
  border-radius: 2px;
  border-color: #ffffff;
  transition: .2s;
  font-family: 'Montserrat';
}

.TodoForm-button--add {
  background: none;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button--add:hover {
  color: #000000;
  font-weight: bold;
  background: #ffffff;
  border: none;
  transition: .2s;
}

.TodoForm-button--cancel {
  display: none;
}

@media (max-width: 770px) {
  form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
  }
  
  textarea {
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button--add {
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  }
  
  .TodoForm-button--cancel {
    background: transparent;
  }
}